import React, { PureComponent } from 'react'
import {
  Table,
  Modal,
  message
} from 'antd'
const confirm = Modal.confirm
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'

@connect(
  state => ({ ...state.storePriority, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '操作',
      width: 100,
      dataIndex: '',
      key: '',
      render: (text, record, index) => <div className="tableOperation">
        <span className="blue" onClick={() => { this.onEdit(record) }}>门店列表</span>
        <span className="blue" onClick={() => { this.moveUpOrDown(text, record, index, { sortNumberType: 'UP' }) }}>上移</span>
        <span className="blue" onClick={() => { this.moveUpOrDown(text, record, index, { sortNumberType: 'DOWN' }) }}>下移</span>
        <span className="red" onClick={() => { this.onDel(record) }}>删除</span>
      </div>
    }, {
      title: '序号',
      width: 50,
      dataIndex: 'sortNumber',
      key: 'sortNumber',
    }, {
      title: '门店类型',
      width: 150,
      dataIndex: 'merchantType',
      key: 'merchantType',
    }]
  }

  onEdit(record) {
    const { update, thisTableData } = this.props
    update({ isShowModal: true, modalTitle: '门店列表', modalData: record })
  }

  // 删除列表项
  onDel(record) {
    const { handleGetList, handleDel } = this.props
    confirm({
      title: '温馨提示',
      content: '是否删除该条信息？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleDel({
          urlParams: [record.id]
        }, () => {
          message.success('删除成功')
          handleGetList()
        })
      }
    })
  }

  //根据参数sortNumberType判断是 上移还是 下移
  moveUpOrDown(text, record, index, sortNumberType) {
    const { handleUpOrDown, handleGetList, total } = this.props
    let n = index + 1
    record = { ...record, ...sortNumberType }

    //上移
    if (record.sortNumberType === 'UP') {
      if (n <= 1) {
        message.error('在顶部了，不能上移')
        return
      }
      handleUpOrDown({ data: record }, () => {
        handleGetList()
      })
      message.success('上移成功')
    }

    // 下移
    if (record.sortNumberType === 'DOWN') {
      if (n >= total) {
        message.error('在底部了，不能下移')
        return
      }
      handleUpOrDown({ data: record }, () => {
        handleGetList()
      })
      message.success('下移成功')
    }
  }

  componentDidMount() {
    const { handleGetList, userLoation } = this.props
    if (!userLoation || !userLoation.value) return
    handleGetList()
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, searchParams, page, size, userLoation } = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams } })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({ searchParams: { page, size, ...searchParams, isAsc: true, columns: sorter.field } })
      } else {
        handleGetList({ searchParams: { page, size, ...searchParams, isAsc: false, columns: sorter.field } })
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({ searchParams: { page: pagination.current, size: pagination.pageSize, ...searchParams } })
    }
  }
  render() {
    const {
      pagination,
      tableLoading,
      searchResult
    } = this.props
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{ x: 1050, y: 400 }}
          onChange={this.onChange}
          pagination={false}
        />
      </div>
    )
  }
}



