import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Form,
  Button,
  message,
  Table,
  Input
} from 'antd'
import {Import} from 'HOC'
import style from './style/Form.scss'
import {API_SHOPINFOIMPORT_IMPORT} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.storePriority, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
    this.state = {
      loading_2: false,
      demandAccountFlag: 0,
    }
    this.columns = [{
      title: '序号',
      width: 50,
      dataIndex: 'sortNumber',
      key: 'sortNumber',
      render: text => <a href="#">{text}</a>
    },{
      title: '门店编号',
      width: 100,
      dataIndex: 'shopCode',
      key: 'shopCode'
    },{
      title: '门店名称',
      width: 100,
      dataIndex: 'shopName',
      key: 'shopName'
    }]
  }


  componentDidMount() {
    const {modalData, handleGetTableList} = this.props
    handleGetTableList({searchParams: modalData})
  }
  
  componentWillUnmount() {
    const {thisTableData, update} = this.props
    update({thisTableData: {}})
  }

  // 改变当前的输入值
  onInputChange(values) {
    const {thisTableData, update} = this.props
    update({thisTableData: {...thisTableData, ...values}})
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowModal: false})
  }
  // 重置
  reset = () => {
    const {update, thisTableData} = this.props
    update({thisTableData: {...thisTableData, shopCode: ''}})
  }

  // 查询数据
  query = () => {
    const {handleGetTableList, modalData, thisTableData} = this.props
    handleGetTableList({searchParams: {...modalData, shopCode: thisTableData.shopCode}})
  }
  
  // 导出门店列表
  handleExport = () => {
    const {handleExport, modalData} = this.props
    handleExport({searchParams: {...modalData}}, () => {
      message.success('导出成功')
    })
  }
  // 导入门店列表
  /* 导入成功 */
  handleImportSuccess = res => {
    const {handleGetTableList, modalData} = this.props
      res && res.message === 'ok' && message.success('导入成功')
      handleGetTableList({searchParams: {...modalData}})
    }
    /* 报错导出 */
    handleExprotError = () => {

    }

    // 删除门店
    handleDel() {
      const{selectedRowKeys, handleTableDel, handleGetTableList, modalData, selectedRows} = this.props
      console.log(modalData.merchantType)
      if(selectedRowKeys.length === 0) {
        message.error('请选择需要删除的门店')
        return
      }
      let paramet = {'ids': selectedRowKeys, merchantType: modalData.merchantType}
      handleTableDel({data: paramet},()=>{
        handleGetTableList({searchParams: {...modalData}})
      })
    }

  render() {
    const {
      selectedRowKeys,
      popTableLoading,
      popSearchResult,
      thisTableData: {shopCode}
    } = this.props
    const {modalTitle, form: {getFieldDecorator}, modalTuttonLoading, modalData} = this.props
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        const{update} = this.props
        update({selectedRowKeys, selectedRows})
      },
      selectedRowKeys,
    };
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
        width={720}
      >
        <div className={style.form}>

          <Row>
            <Col span={23} offset={1}>
              <h3>门店列表维护 <span style={{color: 'red'}}>（下载模板请选择【导出】）</span></h3>
            </Col>
            <Col span={24}>
              <div className='commonInput'>
                <label>门店编号：</label>
                <div className='box'>
                  <Input value={shopCode} onChange={(e) => {
                    this.onInputChange({shopCode: e.target.value})
                  }}/>
              </div>
            </div>
            </Col>
          </Row> 
          <Row type="flex" justify="start"> 
            <Col span={5}>
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
            </Col>
            <Col span={5}>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
            </Col>
            <Col span={5}>
              <Button type="primary"  onClick={() => {
                  this.handleExport()}}>导出</Button>
            </Col>
            <Col span={5}>
              <Import
                  action={{api: API_SHOPINFOIMPORT_IMPORT, data:{merchantType: modalData.merchantType}}}
                  onOk={this.handleImportSuccess}
                  onExprotError={this.handleExprotError}
                  beforeImport={() => {
                    const {update} = this.props
                    update({popTableLoading: true})
                  }}
                  onResponse={() => {
                    const {update} = this.props
                    update({popTableLoading: false})
                  }}
                >
              <Button type="primary" className={style.btn}>导入</Button>
            </Import>
            </Col>
            <Col span={4}>
              <Button type="primary"  onClick={() => {
                  this.handleDel()}}>删除</Button>
            </Col>
          </Row>
        </div>
        <div className='table-group'>
          <Table
            rowKey={record => record.id}
            rowSelection={rowSelection}
            loading={popTableLoading}
            columns={this.columns}
            dataSource={popSearchResult}
            onChange={this.onChange}
            scroll={{x: 410, y: 300}}
            pagination={false}
          />
        </div>
      </Modal>
    )
  }
}
export default Form.create()(App)



