import React, { PureComponent } from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input,
  message
} from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'

@connect(
  state => ({ ...state.storePriority }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }

  // 创建新的列表项
  handleAdd() {
    const { handleAddOrEdit, searchParams, handleGetList } = this.props
    handleAddOrEdit({
      data: {
        merchantType: searchParams.merchantType
      }
    }, () => {
      message.success('创建成功')
      handleGetList()
    })
  }


  // 重置
  reset = () => {
    const { update } = this.props
    update({ searchParams: {} })
  }
  render() {
    const {
      modalTuttonLoading,
      searchParams: { merchantType }
    } = this.props

    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <div className='commonInput'>
              <label style={{ width: 75 }}>门店类型：</label>
              <div className='box'>
                <Input value={merchantType} onChange={(e) => {
                  this.onInputChange({ merchantType: e.target.value })
                }} />
              </div>
            </div>
          </Col>
          <Col span={18}>
            <Row type="flex" justify="end" >
              <Button type="primary" loading={modalTuttonLoading} onClick={() => {
                this.handleAdd()
              }}>创建</Button>
              <Button type="primary" style={{ marginLeft: 10 }} onClick={() => {
                this.reset()
              }}>重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



